.nav-link {
    font-size: 13px;
    text-transform: uppercase;
    font-weight: bold;
}

.navbar-dark .navbar-nav .nav-link {
    color: #fff;
    &:hover{
        color: #fff;
    }
}

.nav-item {
    position: relative;
    margin: 0 10px;

    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: #0aa6bd;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out 0s;
    }
    &:hover{
        &::before {
            visibility: visible;
            transform: scaleX(1);
        }
    }
}

/*=================================================================
	Home Slider
==================================================================*/


#home-slider {
    position: relative;
    padding: 0;
}

.mask-overly {
